.exchanges,
.cycles {
  margin-top: 20px;
}

h2 {
  font-size: 24px;
  color: #333;
}

h4 {
  font-size: 16px;
  color: #777;
}

h5 {
  font-size: 18px;
  color: #333;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th,
td {
  padding: 8px;
  border: 1px solid #ccc;
}

th {
  background-color: #f2f2f2;
}

ul {
  list-style-type: none;
  margin-top: 10px;
  padding-left: 0;
}

li {
  margin-bottom: 5px;
}
